@use "../base/variables" as v;
@use "../base/mixins" as m;

.tabs{

    @include m.desktop{
        display: flex;
    }

    button{
        display: block;
        width: 100%;
        border: none;
        margin-bottom: 1rem;
        padding: 1rem;
        background-color: v.$blue;
        color: v.$white;
        font-weight: 700;
        transition: .3s ease-out;
        &:hover{
            cursor: pointer;
            background-color: darken(v.$blue, 15);
        }
        &.active{
            background-color: v.$blueFour;
            color: v.$blue;
            position: relative;
            &::before{
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                border-bottom: solid v.$blue 0.3rem;
                margin-top: 3.8rem;
            }
        }
    }

}