@use "../base/variables" as v;
@use "../base/mixins" as m;

.appointment {
    padding: 1rem;

    &__data {
        margin: 0;
        padding: 1rem;
        color: v.$blueThree;

        span {
            color: v.$darkness;
            font-weight: 700;
        }
    }

    .service-selected {
        margin: 1rem 0;
        padding: 1rem;
        border: solid .1rem v.$blueTwo;
        border-radius: .5rem;

        .service-title {
            margin: 0;
            padding: 1rem;
            text-align: center;
            color: v.$darkness;
            font-weight: 700;
        }
        .service-price{
            display: inline-block;
            margin: 0;
            padding: 1rem;
            border-radius: .5rem;
            background-color: v.$blueTwo;
            color: v.$white;

        }
    }
}