@use "variables" as v;

h1, h2, h3 {
    font-family: v.$fontPrincipal;
    margin: 0 0 calc(5rem / 2) 0 ;
    font-weight: 700;
    color: v.$darkness;
    text-align: center;
}

h1 {
    font-size: 3rem;
}
h2 {
    font-size: 2.6rem;
}
h3 {
    font-size: 2rem;
    text-align: center;
}
p {
    color: v.$darkness;
    font-size: 1.6rem;
}

.page-name{
    margin: 2rem auto 2.5rem auto;
}
.page-description{
    margin: 1rem auto 3rem auto;
    text-align: center;
}
