@use "../base/variables" as v;

.section {
  margin-top: 3rem;
  margin-bottom: 6rem;
  display: none;

  &__title {
    margin-bottom: 0.5rem;
    color: v.$blueThree;
  }

  &__text {
    margin-top: 0;
    margin-bottom: 3rem;
    text-align: center;
    color: v.$blueFive;
  }
}

.show {
  display: block;
}
.hide {
  display: none;
}
