@use "../base/variables" as v;
@use "../base/mixins" as m;

// grid columns
.container-app {
    @include m.tablet {
        @include m.grid(2, 1fr);
        min-height: 100vh;
    }
    // background image
    .image{
        background-image: url("../img/background.jpg");
        background-size: cover;
        background-position: center center;
    }

    .app{
        width: 95%;
        margin: 0 auto;
        @include m.tablet{
            overflow-y: scroll;
            &::-webkit-scrollbar {
                display: none;
            }
        }
    }
    
}

.bar{
    margin-top: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include m.max-tablet{
        margin-top: 5rem;
        margin-bottom: 5rem;
        flex-direction: column;
    }

    p{
        margin: 0;
        @include m.max-tablet{
            margin-bottom: 2rem;
        }
        span{
            font-weight: 600;
        }
    }
    .button-form{
        margin: 0;
        @include m.desktop{
            max-width: 10rem;
            padding: 1rem;
        }
    }
    
}

.services-bar{
    margin-top: 2rem;
    margin-bottom: 5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include m.max-desktop{
        margin-top: 5rem;
        margin-bottom: 5rem;
        flex-direction: column;
    }

    .button-bar{
        margin: 0 0 1rem 0;
        width: 100%;
        @include m.desktop{
            max-width: 18rem;
            padding: 1rem;
        }
    }
    
}
