@use "../base/variables" as v;
@use "../base/mixins" as m;

// formulary
.form{
    width: 90%;
    margin: 0 auto;

    p{
        text-align: center;
        margin: 0;
        color: v.$darkness;
    }
}

// box of label and input login form
.field{
    display: flex;
    margin-bottom: 3rem;
    align-items: center;
    @include m.max-desktop{
        display: flex;
        flex-direction: column;
        label{
            margin-bottom: 1rem;
        }
    }

    label{
        @include m.desktop{
            flex: 0 0 10rem;
        }

    }
    input{
        all: unset;
        padding: .5rem;
        flex: 1;
        border: none;
        border-bottom: .1rem solid darken(v.$gray, 10);
    }
}
