/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  color: #1a1b15;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 700;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.6rem;
}

h3 {
  font-size: 2rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 1.6rem;
}

.page-name {
  margin: 2rem auto 2.5rem auto;
}

.page-description {
  margin: 1rem auto 3rem auto;
  text-align: center;
}

@media (min-width: 768px) {
  .container-app {
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.container-app .image {
  background-image: url("../img/background.jpg");
  background-size: cover;
  background-position: center center;
}
.container-app .app {
  width: 95%;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .container-app .app {
    overflow-y: scroll;
  }
  .container-app .app::-webkit-scrollbar {
    display: none;
  }
}

.bar {
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 768px) {
  .bar {
    margin-top: 5rem;
    margin-bottom: 5rem;
    flex-direction: column;
  }
}
.bar p {
  margin: 0;
}
@media (max-width: 768px) {
  .bar p {
    margin-bottom: 2rem;
  }
}
.bar p span {
  font-weight: 600;
}
.bar .button-form {
  margin: 0;
}
@media (min-width: 1024px) {
  .bar .button-form {
    max-width: 10rem;
    padding: 1rem;
  }
}

.services-bar {
  margin-top: 2rem;
  margin-bottom: 5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1024px) {
  .services-bar {
    margin-top: 5rem;
    margin-bottom: 5rem;
    flex-direction: column;
  }
}
.services-bar .button-bar {
  margin: 0 0 1rem 0;
  width: 100%;
}
@media (min-width: 1024px) {
  .services-bar .button-bar {
    max-width: 18rem;
    padding: 1rem;
  }
}

.section {
  margin-top: 3rem;
  margin-bottom: 6rem;
  display: none;
}
.section__title {
  margin-bottom: 0.5rem;
  color: #42629b;
}
.section__text {
  margin-top: 0;
  margin-bottom: 3rem;
  text-align: center;
  color: #46526b;
}

.show {
  display: block;
}

.hide {
  display: none;
}

.service-list {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .service-list {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.service-list .service {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 0.5rem;
  padding: 1rem;
  background-color: #1a1b15;
  color: #FFFFFF;
  transition-property: transform, background-color;
  transition-duration: 0.3s;
}
@media (max-width: 480px) {
  .service-list .service {
    flex-direction: column;
    justify-content: center;
  }
}
.service-list .service:hover {
  cursor: pointer;
  transform: scale(1.1);
}
.service-list .service__name {
  display: inline-block;
  padding: 1rem;
  margin: 0;
  color: #FFFFFF;
  text-align: center;
}
.service-list .service__price {
  display: inline-block;
  margin: 0;
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  background-color: #FFFFFF;
  color: #4084d3;
  text-align: center;
  font-weight: 700;
}
.service-list .service.selected {
  background-color: #4084d3;
}

.appointment {
  padding: 1rem;
}
.appointment__data {
  margin: 0;
  padding: 1rem;
  color: #42629b;
}
.appointment__data span {
  color: #1a1b15;
  font-weight: 700;
}
.appointment .service-selected {
  margin: 1rem 0;
  padding: 1rem;
  border: solid 0.1rem #4084d3;
  border-radius: 0.5rem;
}
.appointment .service-selected .service-title {
  margin: 0;
  padding: 1rem;
  text-align: center;
  color: #1a1b15;
  font-weight: 700;
}
.appointment .service-selected .service-price {
  display: inline-block;
  margin: 0;
  padding: 1rem;
  border-radius: 0.5rem;
  background-color: #4084d3;
  color: #FFFFFF;
}

.appointments {
  list-style: none;
  padding: 0;
}
@media (min-width: 768px) {
  .appointments__data {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.appointments li {
  transition: 0.2s;
  margin-bottom: 2rem;
  padding: 1rem;
  border: 2px solid #1a1b15;
  border-radius: 0.5rem;
}
.appointments li h2 {
  padding: 1rem;
  border-bottom: 2px solid #42629b;
}
.appointments li p {
  font-weight: 700;
  color: #4084d3;
}
.appointments li span,
.appointments li .service {
  font-weight: 500;
  color: #1a1b15;
}

.services {
  list-style: none;
  padding: 0;
}
.services li {
  margin-bottom: 2rem;
  padding: 1rem 3rem;
  border: 2px solid #1a1b15;
  border-radius: 0.5rem;
}
.services li p {
  font-weight: 700;
  color: #4084d3;
}
.services li span,
.services li .service {
  font-weight: 500;
  color: #1a1b15;
}
.services li .data,
.services li .actions-services {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .services li .data,
.services li .actions-services {
    flex-direction: column;
  }
  .services li .data .button-update,
.services li .actions-services .button-update {
    margin-bottom: 0;
  }
  .services li .data form,
.services li .actions-services form {
    width: 100%;
  }
}

.form {
  width: 90%;
  margin: 0 auto;
}
.form p {
  text-align: center;
  margin: 0;
  color: #1a1b15;
}

.field {
  display: flex;
  margin-bottom: 3rem;
  align-items: center;
}
@media (max-width: 1024px) {
  .field {
    display: flex;
    flex-direction: column;
  }
  .field label {
    margin-bottom: 1rem;
  }
}
@media (min-width: 1024px) {
  .field label {
    flex: 0 0 10rem;
  }
}
.field input {
  all: unset;
  padding: 0.5rem;
  flex: 1;
  border: none;
  border-bottom: 0.1rem solid #c8c8c8;
}

.button-form {
  padding: 1rem 4rem;
  font-size: 1.6rem;
  display: inline-block;
  background-color: #FFFFFF;
  color: #1a1b15;
  border: 0.2rem solid #1a1b15;
  transition: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 3rem 0;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .button-form {
    width: auto;
  }
}
.button-form:hover {
  background-color: #1a1b15;
  color: #FFFFFF;
  border: 0.2rem solid transparent;
  cursor: pointer;
}

.button-delete {
  padding: 1rem 4rem;
  font-size: 1.6rem;
  display: inline-block;
  background-color: #FFFFFF;
  color: #c22121;
  border: 0.2rem solid #c22121;
  transition: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 3rem 0;
}
@media (min-width: 768px) {
  .button-delete {
    width: auto;
  }
}
.button-delete:hover {
  background-color: #c22121;
  color: #FFFFFF;
  border: 0.2rem solid transparent;
  cursor: pointer;
}

.button-update {
  padding: 1rem 4rem;
  font-size: 1.6rem;
  display: inline-block;
  background-color: #FFFFFF;
  color: #609747;
  border: 0.2rem solid #609747;
  transition: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 3rem 0;
  min-width: none;
}
@media (min-width: 768px) {
  .button-update {
    width: auto;
  }
}
.button-update:hover {
  background-color: #609747;
  color: #FFFFFF;
  border: 0.2rem solid transparent;
  cursor: pointer;
}

.button-bar {
  padding: 1rem 4rem;
  font-size: 1.6rem;
  display: inline-block;
  background-color: #FFFFFF;
  color: #4084d3;
  border: 0.2rem solid #4084d3;
  transition: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 3rem 0;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .button-bar {
    width: auto;
  }
}
.button-bar:hover {
  background-color: #4084d3;
  color: #FFFFFF;
  border: 0.2rem solid transparent;
  cursor: pointer;
}

.actions {
  margin-top: 6rem;
}
@media (min-width: 1024px) {
  .actions {
    width: 90%;
    margin: 6rem auto 1rem auto;
    display: flex;
    justify-content: space-between;
  }
}
.actions a {
  display: block;
  padding: 1rem 0;
  text-align: center;
  color: #1a1b15;
}
.actions a span {
  color: #4084d3;
}

.alert {
  display: block;
  margin-bottom: 1rem;
  padding: 1rem 2rem;
  text-align: center;
  text-transform: uppercase;
}
.alert:hover {
  cursor: pointer;
}
.alert.error {
  color: #c22121;
  border: solid 0.1rem #c22121;
}
.alert.exit {
  color: #609747;
  border: solid 0.1rem #609747;
}

@media (min-width: 1024px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  display: block;
  width: 100%;
  border: none;
  margin-bottom: 1rem;
  padding: 1rem;
  background-color: #52b5e7;
  color: #FFFFFF;
  font-weight: 700;
  transition: 0.3s ease-out;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #1d94d0;
}
.tabs button.active {
  background-color: #ddf5ff;
  color: #52b5e7;
  position: relative;
}
.tabs button.active::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border-bottom: solid #52b5e7 0.3rem;
  margin-top: 3.8rem;
}

.pagination {
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .pagination {
    display: flex;
    justify-content: space-between;
  }
}
.pagination button {
  margin: 0 0 1rem 0;
}
.pagination .hide {
  visibility: hidden;
}