@use "../base/variables" as v;
@use "../base/mixins" as m;

.alert {
    display: block;
    margin-bottom: 1rem;
    padding: 1rem 2rem;
    text-align: center;
    text-transform: uppercase;
    &:hover{
        cursor: pointer;
    }
    &.error {
        color: v.$redTwo;
        border: solid .1rem v.$redTwo;
    }
    &.exit {
        color: v.$green;
        border: solid .1rem v.$green;
    }
}

