@use "../base/variables" as v;
@use "../base/mixins" as m;

.service-list{
    display: grid;
    gap: 2rem;
    @include m.tablet{
        @include m.grid(2, 2rem);
    }

    .service{
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-radius: .5rem;
        padding: 1rem;
        background-color: v.$darkness;
        color: v.$white;
        transition-property: transform, background-color;
        transition-duration: .3s;


        @include m.max-phone{
            flex-direction: column;
            justify-content: center;
        }

        &:hover{
            cursor: pointer;
            transform: scale(1.1);
        }

        &__name{
            display: inline-block;
            padding: 1rem;
            margin: 0;
            color: v.$white;
            text-align: center;

        }
        &__price{
            display: inline-block;
            margin: 0;
            padding: .5rem 1rem;
            border-radius: 1rem;
            background-color: v.$white;
            color: v.$blueTwo;
            text-align: center;
            font-weight: 700;
        }

        &.selected{
            background-color: v.$blueTwo;
        }
    }
}