@use "variables" as v;

/** Media Queries **/
@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin max-phone {
    @media (max-width: v.$phone) {
        @content;
    }
}
@mixin max-tablet {
    @media (max-width: v.$tablet) {
        @content;
    }
}
@mixin max-desktop {
    @media (max-width: v.$desktop) {
        @content;
    }
}


@mixin button($bgColor, $textColor) {
    padding: 1rem 4rem;
    //margin-top: 2rem;
    font-size: 1.6rem;
    //font-weight: 400;
    display: inline-block;
    //font-weight: 700;
    background-color: $textColor;
    color: $bgColor;
    border: .2rem solid $bgColor;
    // transition-property: background-color;
    transition: .3s;
    text-align: center;
    display: block;
    width: 100%;
    //margin: 5rem 0;
    margin: 3rem 0;

    @include tablet {
        width: auto;
    }
    &:hover {
        //background-color: darken( $bgColor, 10% );
        background-color: $bgColor;
        color: $textColor;
        border: .2rem solid transparent;
        cursor: pointer;
    }
}

@mixin grid($columns, $space ) {
    display: grid;
    gap: $space;
    grid-template-columns: repeat($columns, 1fr);
}