@use "../base/variables" as v;
@use "../base/mixins" as m;

.appointments{
    list-style: none;
    padding: 0;
    
    &__data{
        @include m.tablet{
            @include m.grid(2, 2rem);
        }
    }
    li{
        transition: .2s;
        margin-bottom: 2rem;
        padding: 1rem;
        border: 2px solid v.$darkness;
        border-radius: .5rem;
        
        h2{
            padding: 1rem;
            border-bottom: 2px solid v.$blueThree;
        }

        p{
            font-weight: 700;
            color: v.$blueTwo;

        }
        span,
        .service{
            font-weight: 500;
            color: v.$darkness;

        }
        
    }
}
.services{
    list-style: none;
    padding: 0;
    
    li{
        margin-bottom: 2rem;
        padding: 1rem 3rem;
        border: 2px solid v.$darkness;
        border-radius: .5rem;

        p{
            font-weight: 700;
            color: v.$blueTwo;

        }

        span,
        .service{
            font-weight: 500;
            color: v.$darkness;

        }

        .data,
        .actions-services{
            display: flex;
            align-items: center;
            justify-content: space-between;
            @include m.max-tablet{
                flex-direction: column;
                .button-update{
                    margin-bottom: 0;
                }
                form{
                    width: 100%;
                }
            }
            
        }

        
    }
}