@use "../base/variables" as v;
@use "../base/mixins" as m;

.actions{
    margin-top: 6rem;

    @include m.desktop{
        width: 90%;
        margin: 6rem auto 1rem auto;
        display: flex;
        justify-content: space-between;
    }

    a{
        display: block;
        padding: 1rem 0;
        text-align: center;
        color: v.$darkness;
        span{
            color: v.$blueTwo;
        }

    }
}