@use "../base/variables" as v;
@use "../base/mixins" as m;

.button-form{
    @include m.button(v.$darkness, v.$white);
    margin-bottom: 1rem;
}
.button-delete{
    @include m.button(v.$redTwo, v.$white);
}
.button-update{
    @include m.button(v.$green, v.$white);
    min-width: none;
}
.button-bar{
    @include m.button(v.$blueTwo, v.$white);
    margin-bottom: 1rem;
}
